<template>
  <div id="contract_detail">
    <h1 class="fw-bold">Chi tiết hợp đồng</h1>
    <!-- <pre>
      {{ contract }}
    </pre> -->
    <div class="row">
      <div class="col-10">
        <ul class="list-group">
          <li class="list-group-item bg-light fw-bold">THÔNG TIN CÔNG TY</li>
          <li class="list-group-item">
            <strong>Tên công ty:</strong> {{ contract.company_name }}
            <router-link
              :to="{
                name: 'company-edit',
                params: { id: contract.company_id },
              }"
            >
              <div class="btn btn-primary-soft btn-round me-1 mb-1 mb-md-0 btn-sm">
                <i class="fa-thin fa-pen-to-square"></i>
              </div>
            </router-link>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-6">
                <strong>Mã số thuế :</strong> {{ contract.company_taxcode }}
              </div>
              <div class="col-6">
                <strong>Ngày cấp :</strong>
                {{ contract.company_date_create | moment("DD/MM/YYYY") }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <strong>Địa chỉ:</strong> {{ contract.company_address }}
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-6">
                <strong> Cơ quan thuế quản lý :</strong>
                {{ contract.company_tax_manager }}
              </div>
              <div class="col-6">
                <strong>Ngành nghề kinh doanh :</strong> {{ contract.company_type }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-4">
                <strong>Người đại diện: </strong>
                {{ contract.company_sex }}
                {{ contract.company_owner }}
              </div>
              <div class="col-4">
                <strong>Chức vụ :</strong> {{ contract.company_position }}
              </div>
              <div class="col-4">
                <strong>Ngày sinh :</strong>
                {{ contract.owner_birthday | moment("DD/MM/YYYY") }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-4">
                <strong>Số điện thoại :</strong>
                {{ contract.company_phone }}
              </div>
              <div class="col-4">
                <strong>Di động :</strong> {{ contract.company_telephone }}
              </div>
              <div class="col-4">
                <strong>Email :</strong> {{ contract.company_email }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-4">
                <strong>CCCD/CMNĐ :</strong>
                {{ contract.company_card_id }}
              </div>
              <div class="col-4">
                <strong> Ngày cấp :</strong>
                {{ contract.company_card_date | moment("DD/MM/YYYY") }}
              </div>
              <div class="col-4">
                <strong>Nơi cấp :</strong> {{ contract.company_card_issued }}
              </div>
            </div>
          </li>
          <li class="list-group-item bg-light fw-bold">THÔNG TIN HỢP ĐỒNG</li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-6">
                <strong> Số hợp đồng :</strong> {{ contract.contract_number }}/{{
                  contract.contract_number_pattern
                }}
              </div>
              <div class="col-3">
                <strong>Ngày ký :</strong>
                {{ contract.contract_date | moment("DD/MM/YYYY") }}
              </div>
              <div class="col-3">
                <strong>Mã LĐ/Mã TT:</strong> {{ contract.contract_code }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-4">
                <strong>Tên người liên hệ :</strong> {{ contract.customer_name }}
              </div>
              <div class="col-4">
                <strong>Số điện thoại :</strong> {{ contract.customer_phone }}
              </div>
              <div class="col-4">
                <strong>Email : </strong>{{ contract.customer_email }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-6">
                <strong>Ngày bắt đầu :</strong>
                {{ contract.contract_date_start | moment("DD/MM/YYYY") }}
              </div>
              <div class="col-6">
                <strong>Ngày kết thúc :</strong>
                {{ contract.contract_date_end | moment("DD/MM/YYYY") }}
              </div>
            </div>
          </li>
          <li class="list-group-item">
            <div class="row">
              <div class="col-6">
                <strong>Mã BHXH/Số lượng HĐ :</strong>
                {{ contract.extra1 }}
              </div>
              <div class="col-6">
                <strong>Ký hiệu/Mẫu số</strong>
                {{ contract.extra2 }}
              </div>
            </div>
          </li>
          <li class="list-group-item" v-if="contract.details.length > 0">
            <div class="table-responsive">
              <table
                class="table table-dark-gray align-middle p-4 mb-0 table-hover w-100"
              >
                <thead class="table-dark">
                  <tr>
                    <th class="rounded-start">#</th>
                    <th>Tên dịch vụ</th>
                    <th>ĐVT</th>
                    <th>Tiền trước thuế</th>
                    <th>Tiền thuế</th>
                    <th>Số lượng</th>
                    <th>Tổng tiền</th>
                    <th class="rounded-end">Mã TB</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in contract.details" :key="item.detail_id">
                    <td>{{ index + 1 }}</td>
                    <td>{{ item.detail_content }}</td>
                    <td>{{ item.detail_unit }}</td>
                    <td>{{ getPrice(item) }}</td>
                    <td>
                      {{ Math.round(item.detail_product_price * 0.1) | formatamout(0) }}
                    </td>
                    <td>
                      {{
                        item.detail_quantity < 10
                          ? "0" + item.detail_quantity
                          : item.detail_quantity
                      }}
                    </td>

                    <td>
                      {{ subTotal(item) | formatamout }}
                    </td>
                    <td>{{ item.ma_tb }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </li>
          <li class="list-group-item">
            <strong>Tổng giá trị hợp đồng:</strong>
            {{ contract.contract_total | formatamout(0) }} (Bằng chữ:
            {{ contract.contract_price_text }})
          </li>
          <li class="list-group-item bg-light">
            <span class="fw-bold m-0">Danh sách phiếu đăng ký</span>
          </li>
          <li class="list-group-item">
            <div class="form-group">
              <input
                type="text"
                placeholder="Tìm kiếm"
                class="form-control"
                v-model="keywordRegistration"
              />
            </div>
          </li>
          <li class="list-group-item" v-show="contract.registrations.length > 0">
            <table class="table">
              <thead class="table-dark">
                <tr>
                  <th class="rounded-start">#</th>
                  <th width="40%">Nội dung</th>
                  <th width="15%">Chức vụ</th>
                  <th>Số CCCD</th>
                  <th>Ngày cấp</th>
                  <th>Nơi cấp</th>
                  <th>SĐT</th>
                  <th>Mã đăng ký</th>
                  <th class="rounded-end">Email</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in filterRegistrations" :key="item.reg_id">
                  <td>{{ index + 1 }}</td>
                  <td>{{ item.reg_fullname }}</td>
                  <td>{{ item.reg_position }}</td>
                  <td>{{ item.card_id }}</td>
                  <td>{{ item.card_date_create }}</td>
                  <td>{{ item.card_issued }}</td>
                  <td>{{ item.reg_phone }}</td>
                  <td>{{ item.ma_tb }}</td>
                  <td>{{ item.reg_email }}</td>
                </tr>
              </tbody>
            </table>
          </li>

          <li class="list-group-item bg-light">
            <router-link
              :to="{ name: 'contract-index' }"
              class="btn btn-outline-secondary"
            >
              <i class="fa-thin fa-arrow-left"></i> Quay lại
            </router-link>
            <router-link
              :to="{
                name: 'contract-edit',
                params: { id: contract.contract_id },
              }"
              class="btn btn-primary ms-1"
            >
              <i class="fa-thin fa-pen-to-square"></i> Sửa hợp đồng
            </router-link>
            <router-link
              title="Tùy chỉnh hợp đồng"
              class="btn btn-secondary ms-1"
              :to="{
                name: 'contract-option',
                params: {
                  id: contract_id,
                },
              }"
            >
              <i class="fa-regular fa-gear"></i> Tùy chỉnh hợp đồng
            </router-link>
          </li>
        </ul>
      </div>

      <div class="col-2">
        <ul class="list-group">
          <li class="list-group-item bg-light fw-bold">Danh sách mẫu hợp đồng</li>
          <li
            class="list-group-item"
            v-for="template in contract.templates"
            :key="template.temp_id"
          >
            <button
              class="btn btn-primary-soft"
              @click="downloads(contract.contract_id, template)"
            >
              <i class="fa-thin fa-cloud-arrow-down"></i> {{ template.temp_name }}
            </button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      contract: {
        details: {},
        registrations: [],
      },
      contract_id: 0,
      keywordRegistration: "",
    };
  },
  computed: {
    filterRegistrations() {
      return this.contract.registrations.filter((x) => {
        return (
          x.reg_fullname.toLowerCase().indexOf(this.keywordRegistration.toLowerCase()) !==
          -1
        );
      });
    },
  },
  created() {
    this.contract_id = this.$route.params.id;
    this.getDetail();
  },
  methods: {
    async downloadPDF() {
      await axios
        .post("exportpdf", {
          responseType: "blob",
        })
        .then((response) => {
          const pdfBlob = new Blob([response.data], { type: "application/pdf" });
          const link = document.createElement("a");
          link.href = window.URL.createObjectURL(pdfBlob);
          link.download = "invoice.pdf";
          link.click();
        });
    },
    formatAmout(number, digist = 0) {
      return this.$econtract.formatAmout(number, digist);
    },
    formatDate(date) {
      return this.$econtract.formatDate(date);
    },
    async getDetail() {
      this.$econtract.showLoading();
      await axios.get(`/contracts/${this.contract_id}`).then((response) => {
        this.contract = response.data;
        this.$econtract.hideLoading();
      });
    },
    getPrice(item) {
      if (item.detail_product_price > 0)
        return this.formatAmout(item.detail_product_price,4);
      return item.product.product_name;
    },
    subTotal(item) {
      let quantity = parseInt(item.detail_quantity);
      let price = parseFloat(item.detail_product_price);
      let component = parseFloat(item.detail_product_component);
      let total = quantity * +price;
      return Math.floor(Math.round(total * 1.1));
    },
    downloads(contract_id, template) {
      let company_taxcode = this.contract.company_taxcode;
      this.$econtract.showLoading();
      let temp_id = template.temp_id;
      axios
        .post(
          `/exportcontract`,
          {
            contract_id: contract_id,
            temp_id: temp_id,
          },
          {
            responseType: "blob",
          }
        )
        .then((response) => {
          let filename = `${company_taxcode}-${template.temp_display}.docx`;
          const blob = new Blob([response.data], {
            type: "application/msword",
          });
          const url = window.URL.createObjectURL(blob);
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", filename);
          document.body.appendChild(link);
          link.click();
          this.$econtract.hideLoading();
        });
    },
  },
};
</script>
